import { useState } from 'react'
import styled from 'styled-components'
import { Button } from 'antd'
import 'antd/dist/antd.compact.css'

import Camera from './Camera'

const Wrapper = styled.div`
  position: relative;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #333;
  font-size: 16px;
  letter-spacing: 0.02em;

  .content {
    padding: 0 0 120px 0;
    overflow: hidden;

    .title {
      font-size: 25px;
      font-weight: 700;
      letter-spacing: 0.02em;
      line-height: 1.4em;
      text-align: left;
      white-space: pre-wrap;
      margin-top: 25px;
      margin-bottom: 0;
      padding: 0 30px;
    }

    .description {
      font-size: 13px;
      font-weight: 400;
      letter-spacing: 0.02em;
      line-height: 1.6em;
      text-align: left;
      white-space: pre-wrap;
      margin-top: 14px;
      margin-bottom: 0;
      padding: 0 30px;
    }

    .action {
      position: fixed;
      left: 0;
      right: 0;
      bottom: 0;
      margin: 0;
      padding: 30px;
      background-color: #fff;
      overflow: hidden;
      z-index: 2000;

      button {
        width: 100%;
        height: 50px;
      }

      .label {
        font-size: 16px;
        letter-spacing: 0.05em;
        line-height: 1em;
      }
    }
  }
`

const App = () => {
  const [isOpen, setIsOpen] = useState(false)

  const handleTake = () => {
    setIsOpen(true)
  }

  const handleCloseCamera = () => {
    setIsOpen(false)
  }

  console.log('isOpen', isOpen)
  return (
    <>
      <Camera isOpen={isOpen} onClose={handleCloseCamera} />
      <Wrapper>
        <div className="content">
          <div className="title">Title</div>
          <div className="description">
            Our doubts are traitors and make us lose the good we oft might win
            by fearing to attempt.
          </div>
          <div className="action">
            <Button type="primary" size="large" onClick={handleTake}>
              <span className="label">Take</span>
            </Button>
          </div>
        </div>
      </Wrapper>
    </>
  )
}

export default App
