import React from 'react'
import ReactDOM from 'react-dom/client'
import App from './App'

function renderOutsideApp(props, element) {
  const root = ReactDOM.createRoot(document.getElementById(element))
  root.render(
    <React.StrictMode>
      <App {...props} />
    </React.StrictMode>
  )
}

window.OutsideApp = { renderOutsideApp }

renderOutsideApp({}, 'root')
