import { useState, useRef, useEffect } from 'react'
import styled from 'styled-components'
import classNames from 'classnames'
import * as stream from 'stream'

const Wrapper = styled.div`
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  z-index: 3000;
  background-color: black;

  video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    z-index: -1;
    visibility: hidden;
    background-color: transparent;
  }

  .canvas-full {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 3002;
    background-color: transparent;
    perspective: 1px;
    transform: scale(-1, 1);
  }

  .canvas-result {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 3001;
    background-color: transparent;
    perspective: 1px;
    transform: scale(-1, 1);
  }

  .flash {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    transition: opacity 0.9s ease-out;
    z-index: 3010;
  }

  .flash.do-transition {
    opacity: 0;
    background-color: white;
  }

  .take-photo {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: black;
    opacity: 0.5;
    left: 0;
    right: 0;
    bottom: 0;
    height: 140px;
    overflow: hidden;
    z-index: 3030;
  }

  .take-photo-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    font-size: 30px;
    border-radius: 60px;
    border: none;
    outline: none;
    background-color: white;
    color: black;
  }

  .display-none {
    display: none;
  }
`

const Camera = (props) => {
  const { onClose } = props
  const streamRef = useRef()
  const videoRef = useRef()
  const canvasRef = useRef()
  const resultCanvasRef = useRef()
  const requestAnimationRef = useRef()
  const [isShowWhiteFlash, setShowWhiteFlash] = useState(false)
  const [isTaken, setIsTaken] = useState(false)
  const [isReadyToTake, setIsReadyToTake] = useState(false)

  const handleTakePhoto = () => {
    onClose()
  }

  const renderCanvas = (vfWidth, vfHeight) => {
    if (canvasRef.current && videoRef.current) {
      const ctx = canvasRef.current.getContext('2d')
      if (
        vfWidth / vfHeight <
        videoRef.current.videoWidth / videoRef.current.videoHeight
      ) {
        canvasRef.current.width =
          videoRef.current.videoHeight * (vfWidth / vfHeight)
        canvasRef.current.height = videoRef.current.videoHeight

        ctx.drawImage(
          videoRef.current,
          (videoRef.current.videoWidth -
            videoRef.current.videoHeight * (vfWidth / vfHeight)) /
            2,
          0,
          videoRef.current.videoHeight * (vfWidth / vfHeight),
          videoRef.current.videoHeight,
          0,
          0,
          videoRef.current.videoHeight * (vfWidth / vfHeight),
          videoRef.current.videoHeight
        )
      } else {
        canvasRef.current.width = videoRef.current.videoWidth
        canvasRef.current.height =
          videoRef.current.videoWidth * (vfWidth / vfHeight)

        ctx.drawImage(
          videoRef.current,
          0,
          (videoRef.current.videoHeight -
            videoRef.current.videoWidth * (vfWidth / vfHeight)) /
            2,
          videoRef.current.videoWidth,
          videoRef.current.videoWidth * (vfWidth / vfHeight),
          0,
          0,
          videoRef.current.videoWidth,
          videoRef.current.videoWidth * (vfWidth / vfHeight)
        )
      }
    }
  }

  const requestAnimation = () => {
    const loop = () => {
      renderCanvas(window.innerWidth, window.innerWidth)
      requestAnimationRef.current = requestAnimationFrame(loop)
    }
    requestAnimationRef.current = requestAnimationFrame(loop)
  }

  useEffect(() => {
    const constraints = {
      audio: false,
      video: {
        facingMode: 'face',
        width: { ideal: 4096 },
        height: { ideal: 2160 },
      },
    }
    navigator.mediaDevices.getUserMedia(constraints).then((stream) => {
      console.log('getUserMedia starts.')
      streamRef.current = stream
      const video = document.querySelector('video')
      try {
        video.srcObject = stream
        video.onloadedmetadata = function (e) {
          console.log('It is ready to take photos.')
          setIsReadyToTake(true)
          requestAnimation()
        }
      } catch (e) {
        console.log('Video binding error.', e)
      }
    })

    return () => {
      if (requestAnimationRef.current) {
        cancelAnimationFrame(requestAnimationRef.current)
      }
      if (streamRef.current) {
        streamRef.current.getTracks().forEach((track) => {
          track.stop()
        })
      }
    }
  }, [])

  const flashDoTransition = isShowWhiteFlash ? 'do-transition' : ''
  const flashClass = `flash ${flashDoTransition}`

  return (
    <Wrapper>
      <video
        ref={videoRef}
        autoPlay
        muted
        playsInline
        className={classNames({ 'display-none': isTaken })}
      />
      <canvas
        ref={canvasRef}
        className={classNames({
          'canvas-full': true,
          'display-none': isTaken,
        })}
        style={{
          top: (window.innerHeight - window.innerWidth - 140 - 20 - 46) / 2,
          left: 0,
          width: window.innerWidth,
          height: window.innerWidth,
        }}
      />
      <canvas
        ref={resultCanvasRef}
        className={classNames({
          'canvas-result': true,
          'display-none': !isTaken,
        })}
        style={{
          top: (window.innerHeight - window.innerWidth - 140 - 20 - 46) / 2,
          left: 0,
          width: window.innerWidth,
          height: window.innerWidth,
        }}
      />
      <div className="take-photo">
        <button className="take-photo-btn" onClick={handleTakePhoto} />
      </div>
    </Wrapper>
  )
}

const CameraWrapper = (props) => {
  const { isOpen, onClose } = props
  return isOpen ? <Camera onClose={onClose} /> : <div />
}

export default CameraWrapper
